<template>
  <table class="section-table__table">
    <tbody>
      <tr>
        <th>P</th>
        <!-- <th class="hide-xs">No</th> -->
        <!-- <th colspan="2">Name</th> -->
        <th>Team</th>
        <th class="right-align">Wins</th>
        <th class="right-align">Pts</th>
      </tr>
      <tr
        v-for="(result, index) in constructorStandingsData.ConstructorStandings"
        :key="result.index"
      >
        <td>{{ result.positionText }}</td>
        <!-- <td class="hide-xs section-table__no">{{ result.constructor.permanentNumber }}</td> -->
        <td class="section-table__flag">
          <img :src="getFlagImgUrl(index)" alt />
          <span class="section-table__nationality">{{ result.Constructor.nationality }}</span>
        </td>
        <!-- <td class="section-table__name">
          <a
            target="_blank"
            rel="noopener noreferrer"
            :data-constructor="`${result.constructor.givenName} ${result.constructor.familyName}`"
            :href="`${result.constructor.url}`"
          >
            <span class="section-table__given-name">{{ result.constructor.givenName }}</span>
            {{ result.constructor.familyName }}
          </a>
        </td>-->
        <td class="section-table__constructor">
          <span>
            <a
              target="_blank"
              rel="noopener noreferrer"
              :data-constructor="result.Constructor.name"
              :href="result.Constructor.url"
            >{{ result.Constructor.name }}</a>
          </span>
        </td>
        <td class="right-align" v-if="result.wins">{{ result.wins }}</td>
        <td class="right-align" v-if="result.points">{{ result.points }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: 'constructor-standings-table',
  props: {
    constructorStandingsData: [Object, Boolean]
  },
  methods: {
    getFlagImgUrl (i) {
      // const dataLength = this.constructorStandingsData.ConstructorStandings.length
      // if (i === dataLength) return
      // asfmpaefnofgnpsg, ]#ef
      // sg
      // g
      // console.log(dataLength)
      const fallback = require.context('@/assets/img/', false, /\.png$/)
      const flagImages = require.context('@/assets/img/flags/', false, /\.svg$/)
      let flagImage = ''
      try {
        flagImage = flagImages(`./${this.constructorStandingsData.ConstructorStandings[i].Constructor.nationality}.svg`)
      } catch (error) {
        flagImage = fallback('./fallback.png')
      }
      return flagImage
    }
  }
}
</script>
