<template>
  <table v-if="qualifyingResults.length" class="section-table__table" ref="defaultTable">
    <tbody>
      <tr>
        <th>P</th>
        <th colspan="2">Name</th>
        <th
          :class="['section-table__constructor', {'hide-xs': qualifyingResults[0].Q2 || qualifyingResults[0].Q3}]"
        >Team</th>
        <th class="right-align">
          <button
            :class="['section-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'Q1', 'active--desc': currentSortDir === 'desc' && currentSort === 'Q1'}]"
            @click="sortResults('Q1')"
          >Q1</button>
        </th>
        <th class="right-align">
          <button
            :class="['section-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'Q2', 'active--desc': currentSortDir === 'desc' && currentSort === 'Q2'}]"
            @click="sortResults('Q2')"
          >Q2</button>
        </th>
        <th class="right-align">
          <button
            :class="['section-table__sortable', {'active--asc': currentSortDir === 'asc' && currentSort === 'Q3', 'active--desc': currentSortDir === 'desc' && currentSort === 'Q3'}]"
            @click="sortResults('Q3')"
          >Q3</button>
        </th>
      </tr>
      <tr v-for="(result, index) in qualifyingResults" :key="result.index">
        <td>{{ result.position }}</td>
        <td class="section-table__flag">
          <img :src="getFlagImgUrl(index)" alt />
          <span class="section-table__nationality">{{ result.Driver.nationality }}</span>
        </td>
        <td>
          <span class="section-table__given-name">{{ result.Driver.givenName }}</span>
          {{ result.Driver.familyName }}
        </td>
        <td
          :class="['section-table__constructor', {'hide-xs': qualifyingResults[0].Q2 || qualifyingResults[0].Q3}]"
        >
          <span :data-constructor="result.Constructor.name">{{ result.Constructor.name }}</span>
        </td>
        <td class="right-align" v-if="result.Q1">{{ result.Q1 }}</td>
        <td v-else class="right-align">&nbsp;</td>
        <td class="right-align" v-if="result.Q2">{{ result.Q2 }}</td>
        <td v-else class="right-align">&nbsp;</td>
        <td class="right-align" v-if="result.Q3">{{ result.Q3 }}</td>
        <td v-else class="right-align">&nbsp;</td>
      </tr>
    </tbody>
  </table>
</template>

<script>

export default {
  name: 'qualifying-table',
  props: {
    qualifyingResults: [Array, Boolean]
  },
  data: () => ({
    currentSort: 'Q3',
    currentSortDir: 'asc',
    qualifyingResultsInitialState: []
  }),
  mounted () {
    this.qualifyingResultsInitialState = [...this.qualifyingResults]
    // Object.assign(this.qualifyingResults, this.qualifyingResultsInitialState)
    // this.qualifyingResults = Object.assign({}, this.qualifyingResultsInitialState)
  },
  methods: {
    sortResults: function (e) {
      if (e === this.currentSort) return

      this.currentSort = e

      this.qualifyingResults.sort((a, b) => {
        // Ignore if property is empty
        if (!a[e] || !b[e]) return false
        let modifier = 1
        if (a[e] < b[e]) return -1 * modifier
        if (a[e] > b[e]) return 1 * modifier
      })
    },
    getFlagImgUrl (i) {
      const fallback = require.context('@/assets/img/', false, /\.png$/)
      const flagImages = require.context('@/assets/img/flags/', false, /\.svg$/)
      let flagImage = ''
      try {
        flagImage = flagImages(`./${this.qualifyingResults[i].Driver.nationality}.svg`)
      } catch (error) {
        flagImage = fallback('./fallback.png')
      }
      return flagImage
    }
  }
}
</script>
